.profileBlock {
    margin: 20px;
    display: grid;
    grid-template-columns: 400px 1fr;
}

.profileBlock img {
    width: 350px;
    border-radius: 4px;
}

.items {
    padding-top: 20px;
}

.titleText {
    font-weight: bold;
    font-size: 18px;
    padding-bottom: 10px;
}

.text {
    color: #8995a2;
    padding-bottom: 10px;
}

.editProfileBlock {
    text-align: center;
    margin: 40px;
}

.editProfileItem {
    margin-bottom: 10px;
    align-items: center;
}

.btnContainer {
    padding-right: 20px;
    display: flex;
    justify-content: center;
}

.btnContainer button {
    margin: 10px;
}
